let tailorSize = 240; // 需要截取的尺寸240x240px，此变量要和样式中的240px和120px相对应，120px为此变量的一半，若要修改成其他值一定要一一对应
let	newOffsetX = 0; // 拖动缩放完成后的X轴偏移量
let newOffsetY = 0; // 拖动缩放完成后的Y轴偏移量

export default {
	name: "picture-tailor",
	props: {
		// 图片路径
		pictureSrc: {
			type: String,
			required: true
		}
	},
	data() {
		return {
			offsetX: 0, // 图像初始化的X轴偏移量
			offsetY: 0, // 图像初始化的Y轴偏移量
			img_width: 0, // 图片真实宽度
			img_height: 0, // 图片真实高度
			img_scaling: 1, //图片初始化缩放比例
			scale: 1, // 拖动缩放完成后的图片缩放比例
			scaleMin: 0.5, // 最小缩放值
			isShow: false
		};
	},
	watch: {
		pictureSrc() {
			this.getImgInfo();
		}
	},
	created() {
	},
	methods: {
		// 显示组件
		onShow() {
			this.isShow = true;
		},
		// 隐藏组件
		onHide() {
			this.isShow = false;
		},
		// 初始化图片
		getImgInfo() {
			let ctx = uni.createCanvasContext("picture-canvas",this);
			ctx.draw();
			uni.getImageInfo({
				src: this.pictureSrc,
				success: (res) => {
					console.log(res)
					// 图片宽高
					this.img_width = res.width;
					this.img_height = res.height;
					console.log("----------图片宽高----------")
					console.log(this.img_width);
					console.log(this.img_height)
					// 屏幕可用宽高
					let sysInfo = uni.getSystemInfoSync();
					let windowWidth = sysInfo.windowWidth;
					let windowHeight = sysInfo.windowHeight;
					console.log("-------屏幕可用宽高---------")
					console.log(windowWidth)
					console.log(windowHeight)
					// 计算初始缩放比和最小缩放值
					console.log(tailorSize)
					if (this.img_width < tailorSize || this.img_height < tailorSize) { // 当图片宽或高小于240px时
						let count = this.img_width <= this.img_height ? this.img_width : this.img_height;
						this.img_scaling = count / tailorSize;
						this.scaleMin = 1;
					} else if (this.img_width > windowWidth && this.img_width <= this.img_height) { // 当图片宽度大于屏幕宽度并且图片宽度小于图片高度时
						this.img_scaling = this.img_width / windowWidth;
						this.scaleMin = tailorSize / windowWidth;
					} else {
						let count = this.img_width <= this.img_height ? this.img_width : this.img_height;
						this.scaleMin = tailorSize / count;
						console.log(this.scaleMin)
					}

					// 计算图片居中显示时的偏移量
					this.offsetX = -(this.img_width / this.img_scaling / 2 - tailorSize / 2);
					this.offsetY = -(this.img_height / this.img_scaling / 2 - tailorSize / 2);
					// 获取新的偏移量
					newOffsetX = this.offsetX;
					newOffsetY = this.offsetY;
				}
			})
		},
		// 计算拖动偏移量
		movableChange(e) {
			newOffsetX = e.detail.x
			newOffsetY = e.detail.y
		},
		// 计算缩放比例和偏移量
		movableScale(e) {
			newOffsetX = e.detail.x
			newOffsetY = e.detail.y
			this.scale = e.detail.scale;
		},
		// 截取图片
		createImg() {
			console.log("进来了吗")
			let ctx = uni.createCanvasContext("picture-canvas",this);
			console.log(ctx)
			console.log(this.pictureSrc)
			console.log(newOffsetX)
			console.log(newOffsetY)
			console.log(this.img_width)
			console.log(this.img_width / this.img_scaling * this.scale)
			console.log(this.img_height / this.img_scaling * this.scale)
			ctx.drawImage(this.pictureSrc, newOffsetX, newOffsetY, this.img_width /
				this.img_scaling * this.scale,
				this.img_height / this.img_scaling * this.scale);
				console.log("来到这里吗")
			ctx.draw(false, () => {
				console.log("这里呢")
				this.tempFilePath()
			});
		},
		tempFilePath(){
			console.log("感谢大哥的火箭")
			uni.canvasToTempFilePath({
				// 截取图片尺寸
				destWidth: tailorSize / 2,
				destHeight: tailorSize / 2,
				canvasId: "picture-canvas",
				success: (res) => {
					console.log("大哥进来耍一下吗")
					console.log(res)
					console.log(res.tempFilePath)
					 uni.uploadFile({
					            url: 'https://shopping.itaojia.com/Taojia/file/upload/image.do', 
					            filePath: res.tempFilePath,
					            name: 'file',
					            formData: {
					                'kind':'other',
									'owner':'admin',
									'operator_id':'2',
									'device':'1',
									'state':'1',
									'operator_token':'2u2oLMYICXARXDpC1auQg1qad3ebNmkB_1'
									
									
					            },
					            success: (uploadFileRes) => {
									console.log("看返回数据")
					                console.log(uploadFileRes.data);
									let reslut = JSON.parse(uploadFileRes.data).data[0].url
									console.log(reslut)
									this.isShow = false;
									this.$emit("createImg", reslut)
					            }
					        });
					
					// this.onHide();
					// this.isShow = false;
					// this.$emit("createImg", res.tempFilePath)
				}
			},this);
		}
	}
}
